import crypto from "crypto";

const LASTFM_API_BASE = "https://ws.audioscrobbler.com/2.0";

interface LastfmApiParams {
	[key: string]: string;
}

/**
 * Generate API signature for Last.fm API calls
 * Required for all authenticated API calls
 */
function generateSignature(params: LastfmApiParams, apiSecret: string): string {
	const sortedKeys = Object.keys(params).sort();
	let sigString = "";

	for (const key of sortedKeys) {
		sigString += key + params[key];
	}
	sigString += apiSecret;

	return crypto.createHash("md5").update(sigString).digest("hex");
}

/**
 * Make an authenticated request to Last.fm API
 */
async function callLastfmApi<T>(
	method: string,
	params: LastfmApiParams,
	sessionKey?: string
): Promise<T> {
	const apiKey = process.env.LASTFM_API_KEY;
	const apiSecret = process.env.LASTFM_API_SECRET;

	if (!apiKey || !apiSecret) {
		throw new Error(
			"Last.fm API credentials not configured (LASTFM_API_KEY, LASTFM_API_SECRET)"
		);
	}

	// Build API parameters
	const apiParams: LastfmApiParams = {
		api_key: apiKey,
		method: method,
		format: "json",
		...params,
	};

	// Add session key if provided (for authenticated calls)
	if (sessionKey) {
		apiParams.sk = sessionKey;
	}

	// Generate signature for authenticated calls
	if (sessionKey) {
		apiParams.api_sig = generateSignature(apiParams, apiSecret);
	}

	// Make request
	const queryString = new URLSearchParams(apiParams).toString();
	const url = `${LASTFM_API_BASE}?${queryString}`;

	const response = await fetch(url, {
		method: "GET",
		headers: {
			"Content-Type": "application/json",
		},
	});

	if (!response.ok) {
		throw new Error(
			`Last.fm API error: ${response.status} ${response.statusText}`
		);
	}

	const data = (await response.json()) as {
		error?: number;
		message?: string;
	} & T;

	if (data.error) {
		throw new Error(`Last.fm API error: ${data.message}`);
	}

	return data;
}

/**
 * Get user's recent tracks
 */
export async function getRecentTracks(
	sessionKey: string,
	limit = 10
): Promise<{
	recenttracks: {
		track: Array<{
			name: string;
			artist: { name: string };
			album: { name: string };
			image: Array<{ size: string; "#text": string }>;
			url: string;
			date?: { uts: string; "#text": string };
			loved: string;
		}>;
	};
}> {
	return callLastfmApi<{
		recenttracks: {
			track: Array<{
				name: string;
				artist: { name: string };
				album: { name: string };
				image: Array<{ size: string; "#text": string }>;
				url: string;
				date?: { uts: string; "#text": string };
				loved: string;
			}>;
		};
	}>("user.getRecentTracks", { limit: String(limit) }, sessionKey);
}

/**
 * Get user's top tracks
 */
export async function getTopTracks(
	sessionKey: string,
	period: "overall" | "7day" | "1month" | "3month" | "6month" | "12month" = "overall",
	limit = 10
): Promise<{
	toptracks: {
		track: Array<{
			name: string;
			playcount: string;
			artist: { name: string };
			image: Array<{ size: string; "#text": string }>;
			url: string;
		}>;
	};
}> {
	return callLastfmApi<{
		toptracks: {
			track: Array<{
				name: string;
				playcount: string;
				artist: { name: string };
				image: Array<{ size: string; "#text": string }>;
				url: string;
			}>;
		};
	}>("user.getTopTracks", { period, limit: String(limit) }, sessionKey);
}

/**
 * Get similar artists to a given artist
 */
export async function getSimilarArtists(
	artistName: string,
	limit = 10
): Promise<{
	similarartists: {
		artist: Array<{
			name: string;
			match: string;
			url: string;
			image: Array<{ size: string; "#text": string }>;
		}>;
	};
}> {
	return callLastfmApi<{
		similarartists: {
			artist: Array<{
				name: string;
				match: string;
				url: string;
				image: Array<{ size: string; "#text": string }>;
			}>;
		};
	}>("artist.getSimilar", { artist: artistName, limit: String(limit) });
}

/**
 * Get artist information
 */
export async function getArtistInfo(artistName: string): Promise<{
	artist: {
		name: string;
		url: string;
		playcount: string;
		listeners: string;
		bio: { content: string; links: { link: { text: string; href: string } } };
		image: Array<{ size: string; "#text": string }>;
	};
}> {
	return callLastfmApi<{
		artist: {
			name: string;
			url: string;
			playcount: string;
			listeners: string;
			bio: { content: string; links: { link: { text: string; href: string } } };
			image: Array<{ size: string; "#text": string }>;
		};
	}>("artist.getInfo", { artist: artistName });
}

/**
 * Get track information
 */
export async function getTrackInfo(
	trackName: string,
	artistName: string
): Promise<{
	track: {
		name: string;
		artist: { name: string; url: string };
		url: string;
		playcount: string;
		listeners: string;
		userplaycount?: string;
		userloved: string;
		image: Array<{ size: string; "#text": string }>;
		album?: {
			artist: string;
			title: string;
			url: string;
			image: Array<{ size: string; "#text": string }>;
		};
		wiki?: { content: string };
	};
}> {
	return callLastfmApi<{
		track: {
			name: string;
			artist: { name: string; url: string };
			url: string;
			playcount: string;
			listeners: string;
			userplaycount?: string;
			userloved: string;
			image: Array<{ size: string; "#text": string }>;
			album?: {
				artist: string;
				title: string;
				url: string;
				image: Array<{ size: string; "#text": string }>;
			};
			wiki?: { content: string };
		};
	}>("track.getInfo", { track: trackName, artist: artistName });
}

