import { auth } from "~/server/auth";
import { getRecentTracks } from "~/server/auth/lastfm";
import { db } from "~/server/db";

export const runtime = "nodejs";

/**
 * GET /api/music/current
 * Get the user's most recently played track
 */
export async function GET(request: Request) {
	try {
		// Get session
		const session = await auth();

		if (!session?.user?.email) {
			return Response.json({ error: "Unauthorized" }, { status: 401 });
		}

		// Get user's account with Last.fm credentials
		const user = await db.user.findUnique({
			where: { email: session.user.email },
			include: { accounts: true },
		});

		if (!user) {
			return Response.json(
				{ error: "User not found" },
				{ status: 404 }
			);
		}

		const lastfmAccount = user.accounts.find(
			(account) => account.provider === "lastfm"
		);

		if (!lastfmAccount?.access_token) {
			return Response.json(
				{ error: "Last.fm not connected" },
				{ status: 400 }
			);
		}

		// Get recent tracks
		const recentTracks = await getRecentTracks(
			lastfmAccount.access_token,
			1
		);

		const currentTrack = recentTracks.recenttracks?.track?.[0];

		if (!currentTrack) {
			return Response.json({
				track: null,
				message: "No recent tracks found",
			});
		}

		return Response.json({
			track: {
				name: currentTrack.name,
				artist: currentTrack.artist.name,
				album: currentTrack.album.name,
				image:
					currentTrack.image.find((img) => img.size === "large")?.[
						"#text"
					] || currentTrack.image[0]?.["#text"],
				url: currentTrack.url,
				loved: currentTrack.loved === "1",
				playedAt: currentTrack.date?.["#text"],
			},
		});
	} catch (error) {
		console.error("Failed to fetch current track:", error);
		return Response.json(
			{ error: "Failed to fetch current track" },
			{ status: 500 }
		);
	}
}
